html {
    font-size: 100%;
    box-sizing: border-box;
}

*,
*b::before,
*::after {
    box-sizing: inherit;
}

body {
    margin: 0;
}

.section-fs {
    position: relative;
    width: 100%;
    height: 100vh;
}

#video-container {
    width: 100%;

    video {
        width: inherit;
        height: auto;
    }
}

.center-container {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    text-align: center;

    img {
        height: auto;
        width: 60%;
    }
}

#section_0 {
    height: auto;
}

#section_1 {
    background: url("../../assets/images/bg2-01.png") no-repeat 0% 50%;
    /*
        center focus 50% 50%
        left focus 0% 50%
        right focus 100% 50%
    */
    background-size: cover;
    margin-top: -4px;
}

#section_2 {
    background: url("../../assets/images/bg3-01.jpg") no-repeat 0% 50%;
    background-size: cover;
    // margin-top: -4px;
}

#section_3 {
    background: url("../../assets/images/bg4-01.jpg") no-repeat 0% 50%;
    background-size: cover;
    // margin-top: -4px;
}

#section_4 {
    background: url("../../assets/images/bg5-01.jpg") no-repeat 0% 50%;
    background-size: cover;
    // display: flex;
    // justify-content: center;
    // align-items: center;
    // padding: 0 10vh;
}

#section_5 {
    height: auto;
    color: #fff;
}

#akad {
    position: absolute;
    color: #936d58;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);

    p {
        margin: 0;
    }

    h2 {
        font-size: 65px;
    }

    .label {
        font-size: 32px;
    }

    .value {
        font-size: 40px;
        font-weight: bold;
    }

    #g-calendar-btn {
        width: 375px;
        padding: 5px;
        font-size: 24px;
        margin-top: 15px;
        border-color: transparent;
        border-radius: 4px;
        background-color: #936d58;
        color: #fff;
        cursor: pointer;
    }
}

#gmap {
    text-align: center;
    margin-bottom: 632px;
    font-size: 40px;
    color: #936d58;
    font-weight: bold;
}
